:root {
  --calculation-of-one-pixel: calc(1vw + 1vh);
  --pixel: calc(var(--calculation-of-one-pixel) * 0.041789);
}

html,
body,
.page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  transition: all .8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -webkit-transition: all .8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -moz-transition: all .8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  -o-transition: all .8s cubic-bezier(0.54, 0.35, 0.29, 0.99);
  color: #fff;
  overflow: hidden;
}

* {
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.page {
  position: absolute;
  padding-top: calc(var(--pixel) * 30);
  padding-left: calc(var(--pixel) * 30);
}

#p1 {
  left: 0;
}

#p2,
#p3,
#p4,
#p5 {
  left: 200%;
}

#p1 {
  background: rgb(118, 101, 224);
}

#p2 {
  background: #ffc194;
}

#p3 {
  background: #ffe675;
}

#p4 {
  background: rgb(253, 129, 195);
}

#p5 {
  background: rgb(194, 137, 255);
}

#t2:target #p2,
#t3:target #p3,
#t4:target #p4,
#t5:target #p5 {
  transform: translateX(-190%);
  -webkit-transform: translateX(-190%);
  -moz-transform: translateX(-190%);
  -o-transform: translateX(-190%);
  transition-delay: .4s !important;
}

#t2:target #p1,
#t3:target #p1,
#t4:target #p1,
#t5:target #p1 {
  background: black;
}

#t2:target #p1 .menu__icon,
#t3:target #p1 .menu__icon,
#t4:target #p1 .menu__icon,
#t5:target #p1 .menu__icon {
  -webkit-filter: blur(calc(var(--pixel) * 3));
}

.menu__icon {
  color: #fff;
  font-size: calc(var(--pixel) * 23);
  display: block;
}

ul .menu__icon:hover {
  opacity: 0.5;
}

.page .menu__icon .title {
  line-height: calc(var(--pixel) * 50);
}

#t2:target ul .menu__icon,
#t3:target ul .menu__icon,
#t4:target ul .menu__icon,
#t5:target ul .menu__icon {
  transform: scale(.6);
  -webkit-transform: scale(.6);
  -moz-transform: scale(.6);
  -o-transform: scale(.6);
  transition-delay: .25s;
}

#t2:target #dos,
#t3:target #tres,
#t4:target #cuatro,
#t4:target #cinco {
  transform: scale(1.2) !important;
  -webkit-transform: scale(1.2) !important;
  -moz-transform: scale(1.2) !important;
  -o-transform: scale(1.2) !important;
}

ul {
  position: fixed;
  z-index: 1;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  height: calc(var(--pixel) * 280);
  width: 10%;
  padding: 0;
  text-align: center;
}

#menu .menu__icon {
  margin: calc(var(--pixel) * 30) 0;
  transition: all .5s ease-out !important;
  -webkit-transition: all .5s ease-out;
  -moz-transition: all .5s ease-out;
  -o-transition: all .5s ease-out;
}

a {
  text-decoration: none;
}

.title,
.hint {
  display: block;
  color: black;
}

.title {
  font-size: calc(var(--pixel) * 36);
  width: calc(var(--pixel) * 500);
}

.hint {
  font-size: calc(var(--pixel) * 22);
  width: calc(var(--pixel) * 500);
}

#p4 .hint {
  display: inherit !important;
}

.hint a {
  color: yellow;
  transition: all 250ms ease-out;
  -webkit-transition: all 250ms ease-out;
  -moz-transition: all 250ms ease-out;
  -o-transition: all 250ms ease-out;
}

.hint a:hover {
  color: #FFF;
}

.line-trough {
  text-decoration: line-through;
}

.page .menu__icon {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10%;
  left: 0;
  width: calc(var(--pixel) * 270);
  height: calc(var(--pixel) * 170);
  margin: auto;
  text-align: center;
  font-size: calc(var(--pixel) * 80);
  line-height: 1.3;
  transform: translateX(360%);
  -webkit-transform: translateX(360%);
  -moz-transform: translateX(360%);
  -o-transform: translateX(360%);
  transition: all .5s cubic-bezier(.25, 1, .5, 1.25);
  -webkit-transition: all .5s cubic-bezier(.25, 1, .5, 1.25);
  -moz-transition: all .5s cubic-bezier(.25, 1, .5, 1.25);
  -o-transition: all .5s cubic-bezier(.25, 1, .5, 1.25);
}

.page#p1 .menu__icon {
  height: calc(var(--pixel) * 220);
}

.page#p1 .menu__icon {
  transform: translateX(10%) !important;
}

#t2:target .page#p2 .menu__icon,
#t3:target .page#p3 .menu__icon,
#t4:target .page#p4 .menu__icon,
#t5:target .page#p5 .menu__icon {
  transform: translateX(0) !important;
  -webkit-transform: translateX(0) !important;
  -moz-transform: translateX(0) !important;
  -o-transform: translateX(0) !important;
  transition-delay: 1s;
}

//////////////////////   PAGE ARCHIVE   /////////////////////////
.archive {
}

.archive__filder-menu-container-img {
  display: block;
}

.archive__filder-menu-img {
  position: absolute;
  top: calc(var(--pixel)*10);
  left: calc(var(--pixel)* -3);
  rotate: 90deg;
  animation: horizontal-shaking 0.35s infinite;
  cursor: pointer;
}

.archive__filder-menu-img--rotate {
  padding: calc(var(--pixel)*10) calc(var(--pixel)*5);
  rotate: -90deg;
  cursor: pointer;
}

.archive__folder-input {
  width: calc(var(--pixel)*230);
  margin: 10px 0;
}

.archive__folder {
  z-index: 99;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: calc(var(--pixel) * 400);
  height: 100%;
  background-color: #FFF;
}

.archive__folder-menu-form {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  max-width: calc(var(--pixel) * 400);
}

.archive__folder-button {
  width: calc(var(--pixel) * 20);
  height: calc(var(--pixel) * 20);
  margin: calc(var(--pixel) * 10);
  cursor: pointer;
}

.archive__folder-content-container {
  overflow-y: scroll;
  height: calc(var(--pixel) * 800);
}

// archive table folder //
table.iksweb {
  width: 100%;
  height: auto;
  border-collapse: collapse;
  border-spacing: 0;
  overflow: scroll;
  user-select: none;
}

table.iksweb,
table.iksweb td,
table.iksweb th {
  border: 1px solid #595959;
}

table.iksweb td,
table.iksweb th {
  width: calc(var(--pixel)*30);
  height: calc(var(--pixel)*35);
  padding: calc(var(--pixel)*3);
}

table.iksweb th {
  font-weight: normal;
  background: #61616175;
  color: #fff;
}

// БЛОК СТИЛЕЙ ДЛЯ FILE //
.archive__file {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.archive__file-menu {
  display: flex;
  justify-content: space-around;
  align-items: center;
  max-width: calc(var(--pixel) * 1000);
  width: calc(var(--pixel) * 1000);
  padding: calc(var(--pixel) * 15) 0;
}

.archive__file-button {
  width: calc(var(--pixel) * 50);
  height: calc(var(--pixel) * 50);
  padding: 0 calc(var(--pixel) * 10);
  cursor: pointer;
}

// archive table file ///
.scroll-table {
  width: calc(var(--pixel) * 1220);
}

.scroll-table-body {
  height: calc(var(--pixel) * 550);
  width: 100.45%;
  overflow-x: scroll;
  overflow-y: scroll;
  margin-top: 0px;
  margin-bottom: calc(var(--pixel) * 20);
  border-bottom: 1px solid #000000;
}

.scroll-table table {
  width: 100%;
  height: calc(var(--pixel) * 100);
  table-layout: fixed;
  border-collapse: collapse;
  border: none;
}

.scroll-table thead th {
  font-weight: bold;
  text-align: left;
  border: 1px solid black;
  padding: calc(var(--pixel) * 10) calc(var(--pixel) * 15);
  background: #5e59599a;
  font-size: calc(var(--pixel) * 14);
  border-left: 1px solid #000000;
}

.scroll-table tbody td {
  text-align: left;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  background: #0000003a;
  padding: calc(var(--pixel) * 10) calc(var(--pixel) * 15);
  font-size: calc(var(--pixel) * 14);
  vertical-align: top;
}

.scroll-table tbody tr:nth-child(even) {
  background: #080808;
}

/* Стили для скролла */
::-webkit-scrollbar {
  width: calc(var(--pixel) * 6);
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}